const app = getApp();
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
export default {
    components: {
        uniLoadMore
    },
    data() {
        return {
            cateMaskState: 0, //分类面板展开状态
            headerPosition: "fixed",
            headerTop: "0px",
            loadingType: "more", //加载更多状态
            filterIndex: 0,
            cateId: 0, //已选三级分类id
            priceOrder: 0, //1 价格从低到高 2价格从高到低
            cateList: [],
            storeList: [],
            goodsList: [],
            page: 1
        };
    },

    onLoad(options) {





        this.cateId = options.tid;
        this.loadCateList(options.fid, options.sid);
    },
    onShow() {
        if (this.play_media == 1) {
            this.play_media = 0;
        } else {
            this.loadData("refresh");
        }
    },
    onPageScroll(e) {
        //兼容iOS端下拉时顶部漂移
        if (e.scrollTop >= 0) {
            this.headerPosition = "fixed";
        } else {
            this.headerPosition = "absolute";
        }
    },
    //下拉刷新
    onPullDownRefresh() {
        this.loadData("refresh");
    },
    //加载更多
    onReachBottom() {
        this.loadData();
    },
    methods: {
		pickBtn(goods_id){
			console.log(goods_id)
		},

        detailBtn(goods_id) {
            uni.navigateTo({
                url: "/pages/product/product?goods_id=" + goods_id
            });
        },

        async saveBtn(item) {
            const that = this;
            const media_list = item.goodsphoto || [];
            uni.showLoading({
                title: "保存中..."
            });

            let count = 0;
            for (const v of media_list) {
                await this.saveFileToAlbum(v.img_url);
                count++;
                if (count == media_list.length) {
                    uni.hideLoading();
                    const res = await app.request(
                        this.$Url + "/clmsj/goods/savegoodsteam",
                        { goods_id: item.goods_id }
                    );
                    if (res.data.code == 0) {
                        that.$api.msg("保存成功！");
                    }
                }
            }
        },

        saveFileToAlbum(url) {
            return new Promise((resolve, reject) => {
                uni.downloadFile({
                    url: url.replace(/http/, "https"), //仅为示例，并非真实的资源
                    success: res => {
                        if (res.statusCode === 200) {
                            const content_type = res.header[
                                "Content-Type"
                            ].split("/")[0];
                            if (content_type == "image") {
                                uni.saveImageToPhotosAlbum({
                                    filePath: res.tempFilePath,
                                    success: function() {
                                        resolve();
                                    },
                                    fail() {
                                        reject();
                                    }
                                });
                            } else {
                                uni.saveVideoToPhotosAlbum({
                                    filePath: res.tempFilePath,
                                    success: function() {
                                        resolve();
                                    },
                                    fail() {
                                        reject();
                                    }
                                });
                            }
                        }
                    }
                });
            });
        },
        editBtn(id) {
            this.$common.edit_id = id;
            uni.navigateTo({
                url: "/pages/product/add"
            });
        },
        delBtn(id, index) {
            uni.showModal({
                title: "提示",
                content: "确定删除改商品吗？",
                success: res => {
                    if (res.confirm) {
                        uni.request({
                            url: this.$Url + "/clmsj/goods/del_goods",
                            header: {
                                auth: uni.getStorageSync("session_key")
                            },
                            data: {
                                goods_id: id
                            },
                            method: "POST",
                            success: res => {
                                if (res.data.code == 0) {
                                    this.$api.msg("删除成功！");
                                    this.storeList.splice(index, 1);
                                }
                            }
                        });
                    }
                }
            });
        },
        shareBtn(index) {
            console.log(index);
        },
        videoPlay(e) {
            this.play_media = 1;
            const videoContext = uni.createVideoContext(e.currentTarget.id);
            videoContext.requestFullScreen();
        },
        previewImg(list, index) {
            this.play_media = 1;
            let _list = [];
            list.forEach(v => {
                if (v.status == 0) {
                    _list.push(v.img_url);
                }
            });

            console.log(_list);
            uni.previewImage({
                current: list[index].img_url,
                urls: _list
            });
        },
        //加载分类
        async loadCateList(fid, sid) {
            let list = await this.$api.json("cateList");
            let cateList = list.filter(item => item.pid == fid);

            cateList.forEach(item => {
                let tempList = list.filter(val => val.pid == item.id);
                item.child = tempList;
            });
            this.cateList = cateList;
        },
        //加载商品 ，带下拉刷新和上滑加载
        async loadData(type = "add", loading) {
            //没有更多直接返回
            if (type === "add") {
                if (this.loadingType === "nomore") {
                    return;
                }
                this.loadingType = "loading";
            } else {
                this.loadingType = "more";
            }

            if (type === "refresh") {
                this.page = 1;
            }

            uni.request({
                url: this.$Url + "/clmsj/goods/goodsList",
                header: {
                    auth: uni.getStorageSync("session_key")
                },
                data: {
                    team_id: 10000,
                    page: this.page
                },
                method: "POST",
                success: res => {
                    if (res.data.code == 1) {
                        uni.navigateTo({
                            url: "/pages/user/user"
                        });
                        return;
                    }

                    if (res.data.code == 0) {
                        let storeList = res.data.data;
                        this.page++;
                        //筛选，测试数据直接前端筛选了
                        // if (this.filterIndex === 1) {
                        //     storeList.sort((a, b) => b.sales - a.sales);
                        // }
                        // if (this.filterIndex === 2) {
                        //     storeList.sort((a, b) => {
                        //         if (this.priceOrder == 1) {
                        //             return a.price - b.price;
                        //         }
                        //         return b.price - a.price;
                        //     });
                        // }
                        if (type == "refresh") {
                            this.storeList = storeList;
                        } else {
                            this.storeList = this.storeList.concat(storeList);
                        }

                        //判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
                        this.loadingType =
                            storeList.length < 10 ? "nomore" : "more";
                        if (type === "refresh") {
                            if (loading == 1) {
                                uni.hideLoading();
                            } else {
                                uni.stopPullDownRefresh();
                            }
                        }
                    }
                }
            });
        },
        //筛选点击
        tabClick(index) {
            if (this.filterIndex === index && index !== 2) {
                return;
            }
            this.filterIndex = index;
            if (index === 2) {
                this.priceOrder = this.priceOrder === 1 ? 2 : 1;
            } else {
                this.priceOrder = 0;
            }
            uni.pageScrollTo({
                duration: 300,
                scrollTop: 0
            });
            this.loadData("refresh", 1);
            uni.showLoading({
                title: "正在加载"
            });
        },
        //显示分类面板
        toggleCateMask(type) {
            let timer = type === "show" ? 10 : 300;
            let state = type === "show" ? 1 : 0;
            this.cateMaskState = 2;
            setTimeout(() => {
                this.cateMaskState = state;
            }, timer);
        },
        //分类点击
        changeCate(item) {
            this.cateId = item.id;
            this.toggleCateMask();
            uni.pageScrollTo({
                duration: 300,
                scrollTop: 0
            });
            this.loadData("refresh", 1);
            uni.showLoading({
                title: "正在加载"
            });
        },
        //详情
        navToDetailPage(item) {
            //测试数据没有写id，用title代替
            let id = item.store_name;
            uni.navigateTo({
                url: `/pages/product/product?id=${id}`
            });
        },
        stopPrevent() {}
    }
};
